* {
  margin: 0;
  padding: 0;
}
.wrapper {
}
header {
  height: 50px;
  border: 1px solid #000;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.logo {
}
.search-container {
  /* border: 1px solid #000; */
  width: 50%;
  height: 80%;
}
.search-form {
  display: flex;

  height: 100%;
  outline: 1px solid #000;
  border-radius: 5px;
  overflow: hidden;
}
.search-input {
  width: 90%;
  border: none;
  padding-left: 10px;
  outline: none;
}
.search-input:focus {
  box-shadow: inset 1px 1px 8px #47536f;
}
.search-btn {
  /* width: 10%; */
  min-width: 40px;

  background-color: #47536f; /* Green */
  border: none;
  color: white;
  outline: 1px solid #000;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}
